.pagination .page-item {
  cursor: pointer;
  padding: 20px;
  font-size: 20px;
  background-color: #ff9900;
  color: black;
  border: 1px solid black;
}

.pagination .page-item:hover {
  position: relative;
  bottom: 2px;
  box-shadow: 0 5px 10px black;
}

.pagination .active {
  background-color: black;
  color: #ff9900;
}
